.container{
    width: 100%;
    height: 100vh;
    padding-top: 50px;
    background-color: rgb(156, 215, 239);
}
.weather{
    width: 400px;
    padding: 30px;
    border-radius: 10px;
    margin: 50px auto;
    text-align: center;
    color: white;
    background: linear-gradient(130deg, rgb(28, 28, 60), rgb(78, 78, 255));
}
.search{
    display: flex;
    align-items: center;
    justify-content: space-between;   
}
.search input{
    border: none;
    outline: 0;
    padding: 12px 25px;
    font-size: 18px;
    background-color: rgb(222,237,243);
    color: black;
    border-radius: 30px;
    flex: 1;
    margin-right: 12px;
}
.search button{
    border: none;
    outline: 0;
    width: 50px;;
    height: 50px;
    border-radius: 50%;
    cursor: pointer;
}
.search button img{
    width: 20px;
}
.winfo .icon{
    margin-top: 20px;
    width: 170px;
}
.winfo h1{
    font-size:  60px;;
    font-weight: 500;
    margin-top: 5px;
}
.winfo h2{
    font-size: 40px;
    font-weight: 400;
    margin-top: -40px;
}
.winfo img{
    width: 300px;
    height: 300px;
}
.details{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
    margin-top: 50px;
}
.details .col{
    display: flex;
    align-items: center;
    text-align: left;
}
.details .col img{
    width: 50px;
    height: 50px;
    margin-right: 10px;
}
.humidity, .wind{
    font-size: 20px;
}
.humidity p,  .wind p{
    margin: 0px;
}
.error{
    color: rgb(255, 240, 240);
    text-align: left;
    margin-right: 10px;
    font-weight: 600;
}


/* ---------------------Media query------------------- */

@media (max-width: 450px) {
    .weather {
      width: 100%;
      padding: 20px;
      border-radius: 0;
      margin: 0;
    }
    .search {
      flex-direction: column;
      align-items: stretch;
    }
    .search input {
      margin-right: 0;
      margin-bottom: 12px;
    }
    .winfo h1 {
      font-size: 40px;
    }
    .winfo h2 {
      font-size: 30px;
    }
    .winfo img {
      width: 200px;
      height: 200px;
    }
    .details {
      flex-direction: column;
      align-items: stretch;
      padding: 0;
      margin-top: 30px;
    }
    .details .col {
      margin-bottom: 20px;
    }
    .error {
      text-align: center;
      margin-right: 0;
    }
  }
  